Config.registry('node', {
    label: '',
    preview: () => <ElButton circle style={"width: 80px; height: 80px;"}></ElButton>,
    render: ({props}) => <ElButton circle type={props.type} style={"width:"+`${props.size || 80}`+"px;height:"+`${props.size || 80}`+"px;"}>
        {props.text || ''}
    </ElButton>,
    props: {
        text: createEditorInputProp(''),
        type: createEditorSelectProp('', [
            {label: '',  val: '' },
            {label: '',  val: 'primary'},
            {label: '',  val: 'success'},
            {label: '',  val: 'warning'},
            {label: '',  val: 'danger'},
            {label: '',  val: 'info'},
        ]),
        size: createEditorSliderProp(''),
        classes: createEditorSelectProp('Schema', [
            {label: '', val:'Null'},
            {label: '-CreativeWork', val: 'CreativeWork'},
            {label: '-Event', val:'Event'},
            {label: '-Intangible', val:'Intangible'},
            {label: '-MedicalEntity', val:'MedicalEntity'},
            {label: '-Organization', val:'Organization'},
            {label: '-Person', val:'Person'},
            {label: '-Place', val:'Place'},
            {label: '-Product', val:'Product'},
        ]),
        data: createEditorTableProp('Data',{
            options: [
                {key: '', value: 'key'},
                {key: '', value: 'value'},
                {key: '()', value: 'commnets'},
            ],
            showKey: 'key',
            showValue: 'value',
        }),
    }
})

